<template>
  <div id="AddBlogGrowth">
    <form v-if="!summited">
      <!-- 成长标题 -->
      <el-input placeholder="请输入内容" v-model="AddGrowth.title" clearable value-format="yyyy-MM-dd"></el-input>
      <!-- 成长内容 -->
      <el-input type="textarea" autosize placeholder="请输入内容" v-model="AddGrowth.content"></el-input>
      <!-- 提交时间 -->
      <el-button style="display:block;margin-top:10px;" type="success" @click.prevent="post">添加博客</el-button>
    </form>
  </div>
</template>

<script>
export default {
  name: "AddBlogGrowth",
  data() {
    return {
      AddGrowth: {
        title: "",
        content: ""
      }
    };
  },
  created() {},
  methods: {
    post: function() {
      this.$http
        .post(
          "https://wd8560417834unmeck.wilddogio.com/growth.json",
          this.AddGrowth
        )
        .then(function(data) {
          // console.log(data);
          this.$router.push({ path: "/" });
          this.submitSuccessfully();
        });
    },
    submitSuccessfully() {
      this.$notify({
        title: "发布成功",
        message: "您的博客已经发布成功了哦",
        type: "success"
      });
    }
  }
};
</script>