import Vue from 'vue'
import Router from 'vue-router'
import ShowBlogs from './components/Show-Blogs.vue'
import AddBlog from './components/Add-Blog.vue'
import SingleBlog from './components/single-Blog.vue'
import EditBlog from './components/Edit-Blog.vue'
import Introduce from './components/Introduce.vue'
import Growth from './components/Blog-growth.vue'
import AddGrowth from './components/Add-blogGrowth.vue'
Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      // name: 'home',
      component: ShowBlogs
    },
    {
      path: '/add',
      // name: 'home',
      component: AddBlog
    },
    {
      path: '/introduce',
      component: Introduce
    },
    {
      path: '/growth',
      component: Growth
    },
    {
      path: '/addGrowth',
      component: AddGrowth
    },
    {
      path:"/blog/:id",
      component:SingleBlog
    },
    {
      path:"/edit/:id",
      component: EditBlog
    }
  ]
})
